

'use client'

// app/markdown-page/page.js
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm' // 只用这个
import 'highlight.js/styles/github.css' // 只用这个
import { useState } from 'react'
import { ChevronLeft, ChevronRight } from 'lucide-react'

const mdContent = `# Solar Battery Specs`

export default function MarkdownPage() {
  const [currentIndex, setCurrentIndex] = useState(0)
  const images = ["/280w-german-solar-panel.jpg", "/solar/2.jpg", "/solar/3.jpg", "/solar/4.jpg", "/solar/5.jpg"]

return (
  <div className="max-w-5xl mx-auto px-6 py-12 space-y-12">

    {/* ==================== 带左右箭头 + 点击切换的轮播图（基于你原代码改造） ==================== */}
<div className="relative w-full max-w-4xl mx-auto">
  <div className="relative overflow-hidden rounded-2xl shadow-2xl bg-muted aspect-video">
    {/* 当前显示的图片 */}
    {images.map((src, index) => (
      <img
        key={index}
        src={src}
        alt={`太阳能电池 ${index + 1}`}
        className={`absolute inset-0 w-full h-full object-cover transition-opacity duration-500 ${
          index === currentIndex ? "opacity-100" : "opacity-0"
        }`}
      />
    ))}

    {/* 左箭头 */}
    <button
      onClick={() => setCurrentIndex((prev) => (prev === 0 ? images.length - 1 : prev - 1))}
      className="absolute left-4 top-1/2 -translate-y-1/2 p-3 rounded-full bg-black/40 text-white hover:bg-black/70 opacity-0 group-hover:opacity-100 transition-all duration-300 z-10 backdrop-blur-sm"
    >
      <ChevronLeft className="w-7 h-7" />
    </button>

    {/* 右箭头 */}
    <button
      onClick={() => setCurrentIndex((prev) => (prev === images.length - 1 ? 0 : prev + 1))}
      className="absolute right-4 top-1/2 -translate-y-1/2 p-3 rounded-full bg-black/40 text-white hover:bg-black/70 opacity-0 group-hover:opacity-100 transition-all duration-300 z-10 backdrop-blur-sm"
    >
      <ChevronRight className="w-7 h-7" />
    </button>

    {/* 小圆点指示器（可点击） */}
    <div className="absolute bottom-4 left-1/2 -translate-x-1/2 flex gap-3 z-10">
      {images.map((_, index) => (
        <button
          key={index}
          onClick={() => setCurrentIndex(index)}
          className={`transition-all duration-300 ${
            index === currentIndex
              ? "w-10 h-2.5 bg-white rounded-full"
              : "w-2.5 h-2.5 bg-white/60 rounded-full hover:bg-white/90"
          }`}
        />
      ))}
    </div>
  </div>
</div>
<div className="relative group"> {/* 这个 group 用来触发箭头显示 */}
  {/* 上面整个轮播图包在这里 */}
</div>
{/* ==================== 轮播图结束 ==================== */}

    {/* 你原来的 Markdown 内容，完全不动 */}
    <div className="max-w-3xl mx-auto">
      <ReactMarkdown remarkPlugins={[remarkGfm]}>
        {mdContent}
      </ReactMarkdown>
    </div>
  </div>
)
}